.main {
  padding-bottom: 20px;

  &-container {
    gap: 70px;
    @media(max-width: 959px) {
      flex-direction: column;
      gap: 20px;
    }
  }

  &-wrapper {
    width: 740px;
    @media(max-width: 1149px) {
      width: 470px;
      flex-grow: 1;
    }
    @media(max-width: 959px) {
      width: 100%;
    }
    p {
      margin-top: 10px;
    }
    .btn {
      margin-top: 35px;
      width: 100%;
      max-width: 316px;
    }
  }

  &-img {
    img {
      width: auto;
      max-width: 100%;
      display: block;
    }
  }
}
